/*Now the CSS*/
* {
  margin: 0;
  padding: 0;
}

.tree {
  display: flex;
  justify-content: center;
}

.tree ul.tree-unordered-list {
  padding-top: 20px;
  position: relative;

  transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
}

.tree li.tree-list-item {
  float: left;
  text-align: center;
  list-style-type: none;
  position: relative;
  padding: 20px 5px 0 5px;

  transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
}

/*We will use ::before and ::after to draw the connectors*/

.tree li.tree-list-item::before,
.tree li.tree-list-item::after {
  content: "";
  position: absolute;
  top: 0;
  right: 50%;
  border-top: 1px solid #ccc;
  width: 50%;
  height: 20px;
}
.tree li.tree-list-item::after {
  right: auto;
  left: 50%;
  border-left: 1px solid #ccc;
}

/*We need to remove left-right connectors from elements without 
any siblings*/
.tree li.tree-list-item:only-child::after,
.tree li.tree-list-item:only-child::before {
  display: none;
}

/*Remove space from the top of single children*/
.tree li.tree-list-item:only-child {
  padding-top: 0;
}

/*Remove left connector from first child and 
right connector from last child*/
.tree li.tree-list-item:first-child::before,
.tree li.tree-list-item:last-child::after {
  border: 0 none;
}
/*Adding back the vertical connector to the last nodes*/
.tree li.tree-list-item:last-child::before {
  border-right: 1px solid #ccc;
  border-radius: 0 5px 0 0;
  -webkit-border-radius: 0 5px 0 0;
  -moz-border-radius: 0 5px 0 0;
}
.tree li.tree-list-item:first-child::after {
  border-radius: 5px 0 0 0;
  -webkit-border-radius: 5px 0 0 0;
  -moz-border-radius: 5px 0 0 0;
}

/*Time to add downward connectors from parents*/
.tree ul.tree-unordered-list ul.tree-unordered-list::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  border-left: 1px solid #ccc;
  width: 0;
  height: 20px;
}

.tree li.tree-list-item span.node-content {
  /* border: 1px solid #ccc; */
  padding: 5px 10px;
  text-decoration: none;
  color: #666;
  background-color: #fff;
  font-family: arial, verdana, tahoma;
  font-size: 11px;
  display: inline-block;

  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;

  transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  cursor: pointer;
}

.tree li.tree-list-item span.node-inactive {
  /* border: 1px solid red; */
  background-color: transparent;
}

/*Time for some hover effects*/
/*We will apply the hover effect the the lineage of the element also*/
.tree li.tree-list-item span.node-content:hover,
.tree
  li.tree-list-item
  span.node-content:hover
  + ul.tree-unordered-list
  li.tree-list-item
  span.node-content {
  /* background: #c8e4f8;  */
  /* color: #000;  */
  /* border: 1px solid #94a0b4; */
}
/*Connector styles on hover*/
.tree
  li.tree-list-item
  span.node-content:hover
  + ul.tree-unordered-list
  li::after,
.tree
  li.tree-list-item
  span.node-content:hover
  + ul.tree-unordered-list
  li::before,
.tree
  li.tree-list-item
  span.node-content:hover
  + ul.tree-unordered-list::before,
.tree
  li.tree-list-item
  span.node-content:hover
  + ul.tree-unordered-list
  ul.tree-unordered-list::before {
  border-color: rgb(80, 79, 79);
}
