html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

html,
body {
  padding: 0;
  margin: 0;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
}

.topnav-area {
  grid-area: topnav;
  height: 75px;
  width: 100%;
  z-index: 10;
}

.sidebar-area {
  grid-area: sidebar;
}

.breadcrumbs-area {
  grid-area: breadcrumbs;
}

.content-area {
  grid-area: content;
  position: relative;
}

.grid-wrapper {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: 200px calc(100% - 200px);
  grid-template-rows: 75px 75px auto;
  grid-template-areas:
    "topnav topnav"
    "sidebar breadcrumbs"
    "sidebar content";
}

.player-preview-popup-container {
  position: absolute;
  right: 15px;
  bottom: 15px;
  height: 300px;
  width: 300px;
}

.player-preview-popup-iframe-wrapper {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;
}

.player-preview-popup-iframe-wrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}
